import { memo, useCallback, useEffect, useMemo, useState } from "react";
import PropTypes from "prop-types";
import CustomPagination from "@components/customPaginationV1";
import CustomSelectDropdown from "@components/customSelectDropdown";
import { Home, CheckCircle } from "react-feather";
import { injectIntl, FormattedMessage } from "react-intl";
import { Button, Table, notification } from "antd";
import "antd/dist/antd.css";
import "./VerifiedResDatatable.scss";

import { DropdownItem } from "reactstrap";
import { ReactComponent as FilterIcon } from "@src/assets/images/icons/filter.svg";
import { ReactComponent as IconSearch } from "@src/@core/assets/icons/IconSearch.svg";
import Sidebar from "@components/sidebar";
import PickedFilter from "@components/filters/filterComponents/PickedFilter";
import { countNotEmptyProperties } from "@src/helper/object";
import useDebounce from "../../../utility/hooks/useDebounce";

const INITIAL_PROPS = {
  tableHeader: [],
  tableData: [],
  selectedRows: [{ id: 1, address: "" }],
  setSelectedRows: () => {},
  totalRecords: 0,
  totalPages: 1,
  currentPage: 0,
  setCurrentPage: () => {},
};

const GenerateTable = ({
  tableData,
  selectedRows,
  setSelectedRows,
  totalRecords,
  totalPages,
  fetching,
  currentPage,
  setCurrentPage,
  visibleColumns,
  setVisibleColumns,
  sidebarVisibility,
  setSidebarVisibility,
  selectedFilterValues,
  setSelectedFilterValues,
  intl,
} = INITIAL_PROPS) => {
  // rowSelection object indicates the need for row selection
  const rowSelection = {
    onChange: (_, sltRows) => {
      setSelectedRows(sltRows);
    },
  };

  function moveToPicked() {
    // TODO: call api to move selected res to picked list
    selectedRows.forEach((row) =>
      notification.info({
        message:
          intl.formatMessage(
            { id: "ResXMovedTo" },
            { x: `${row.id} | ${row.address}` }
          ) + intl.formatMessage({ id: "verifiedRes" }),
      })
    );
  }

  // Searching
  const [searchTerm, setSearchTerm] = useState("")
  const debounceSearch = useDebounce(searchTerm, 800)
  useEffect(()=>{
    setSelectedFilterValues({
      ...selectedFilterValues,
      searchText: searchTerm,
    })
  },[debounceSearch])

  function toggleSidebar() {
    setSidebarVisibility(!sidebarVisibility);
  }

  return (
    <div className="rr-border-radius-24 rr-background-white">
      <div className="rr-border-radius-24-top rr-background-yellow-3 rr-table-header d-flex flex-column">
        <div className="d-flex align-items-center justify-content-between h-100">
          <div className="d-flex flex-column justify-content-between">
            <div className="d-flex align-items-center">
              <Home size={20} color="#323759" />
              <span className="ml-2 rr-table-header-title rr-color-main">
                <FormattedMessage id="verifiedRes" />
              </span>
              <span className="ml-1 rr-table-header-title-sub rr-color-main">
                <FormattedMessage id="XTotal" values={{ x: totalRecords }} /> {}
              </span>
            </div>
            <div className="d-flex align-items-center color-main table-header-subtitle">
              {selectedRows.length > 0 && (
                <>
                  <FormattedMessage
                    id="selectedXItems"
                    values={{ x: selectedRows.length }}
                  />
                  <CustomSelectDropdown
                    useCustomDropdownItem
                    CustomDropdownItem={() => (
                      <DropdownItem
                        tag="a"
                        href="/"
                        className="w-100"
                        onClick={(e) => e.preventDefault()}
                      >
                        <CheckCircle size={15} />
                        <span className="align-middle ml-50">
                          <FormattedMessage id="pickRes" />
                        </span>
                      </DropdownItem>
                    )}
                    label={<FormattedMessage id="pickAction" />}
                  />
                </>
              )}
            </div>
          </div>
          <div className="d-flex align-items-center">
            <div className="rr-input-wrapper">
              <IconSearch color="white !important" />{" "}
              <input
                className="input-box"
                placeholder={intl.formatMessage({ id: "enterKeyword" })}
                value={searchTerm}
                onChange={({ target: { value } }) =>
                  setSearchTerm(value)
                }
              />
            </div>

            <div className="rr-button-wrapper">
              <Button className="button-box" onClick={toggleSidebar}>
                <FilterIcon className="mr-1" /> <FormattedMessage id="filter" />
                ({countNotEmptyProperties(selectedFilterValues)})
              </Button>
            </div>
          </div>
        </div>
      </div>
      <div className="d-flex flex-column align-items-center">
        <Table
          className="w-100"
          columns={visibleColumns.filter((item) => item.visible)}
          dataSource={tableData}
          rowKey={(record) => Object.values(Object.values(record)[0])[0]}
          rowSelection={rowSelection}
          pagination={false}
          sticky
          scroll={{ x: true }}
          loading={fetching}
        />
        {totalPages ? (
          <CustomPagination
            totalPages={totalPages}
            currentPage={currentPage}
            setCurrentPage={setCurrentPage}
          />
        ) : null}
      </div>
      <Sidebar
        open={sidebarVisibility}
        toggleSidebar={toggleSidebar}
        closeBtn={false}
        width="500"
        title=""
      >
        <PickedFilter
          values={selectedFilterValues}
          setValues={setSelectedFilterValues}
        />
      </Sidebar>
    </div>
  );
};

export default injectIntl(memo(GenerateTable));

GenerateTable.propTypes = {
  tableHeader: PropTypes.arrayOf(PropTypes.object),
  tableData: PropTypes.arrayOf(PropTypes.object),
  selectedRows: PropTypes.arrayOf(PropTypes.object),
  setSelectedRows: PropTypes.func,
  totalRecords: PropTypes.number,
  totalPages: PropTypes.number,
  currentPage: PropTypes.number,
  setCurrentPage: PropTypes.func,
};

GenerateTable.defaultProps = INITIAL_PROPS;
